import React from 'react';
import './App.css';
import {movies} from './movies';
import MoviePreviews from './moviePreviews';
import MovieDetails from './movieDetails';


function App(){

  return (
    <div>
       <Movie/>
    </div>
   
  );
}

function Movie() {
  const [currentMovie, setCurrentMovie]  = React.useState(null) //null is the initial state.. this is the only state we can pass as an argument.
  
//console.log(currentMovie);
  
  
  return (
  <div className="movieStyleDiv">
     
        {
          movies.map((movie) => {

          return (
            
              <MoviePreviews 
              key = {movie.title}
              movie={movie} 
              onClickFunction = {setCurrentMovie}
              
              />
             )
          }
        )
      }
      
      {
        currentMovie ?
          <MovieDetails  currentMovie = {currentMovie} />
       
          :
          <p className="displayP">Please Select a Movie!!</p>
      }
    </div>
  );
}





export default App;
